.btn {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    border-radius: 10px;
    border: 2px solid rgba(255, 255, 255, 0.50);
    background: none;
    color: #FFF;
    font-family: "Rubik";
    font-size: 24px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    padding-right: 2%;
    width: 100%;

    img, p {
        margin-left: 5%;
    }
}

.btn:hover {
    border-radius: 10px;
    border: 2px solid rgba(255, 255, 255, 0.50);
    background: rgba(255, 170, 80, 0.50);
}